    <!-- 筛选区域 -->
    <div class="filter-bar">
        <div class="filter-option">
            <span class="filter-label">证书类型</span>
            <select class="filter-select">
                <option value="all">全部证书</option>
            </select>
        </div>

        <div class="filter-option">
            <span class="filter-label">颁发年份</span>
            <select class="filter-select">
                <option value="all">全部年份</option>
