    <!-- 证书展示区域 -->
    <div class="certificates-grid">
        <!-- 证书卡片   -->
        <div class="certificate-card" v-for="(i) in cardarr" :key="i.id">

            <div class="certificate-header">
                <div class="certificate-icon">
                    <i class="fas fa-award"></i>
                </div>
                <div class="certificate-title">{{ i.CERTIFICATE_NAME }}</div>
                <div class="certificate-subtitle">杰出志愿者荣誉</div>
            </div>
            <div class="certificate-body">
                <div class="certificate-info">
                    <span class="certificate-label">证书编号</span>
                    <span class="certificate-value">{{ (i.CERTIFICATE_ID).split("-").slice(0, 3).join("-") }}
                        <br>-{{ (i.CERTIFICATE_ID).split("-").slice(3).join("-") }}</span>
                </div>

                <div class="certificate-info">
                    <span class="certificate-label">颁发日期</span>
                    <span class="certificate-value">{{ i.CERTIFICATE_DATE.slice(0, 10) }}</span>
                </div>
                <div class="certificate-info">
                    <span class="certificate-label">颁发单位</span>
                    <span class="certificate-value">C&nbsp;V&nbsp;S&nbsp;M&nbsp;S</span>
                </div>
                <div class="certificate-description">
                    张先生过去一年积极参与社区志愿活动，服务时长158小时，表现突出，特授予五星志愿者称号。
                </div>
            </div>
            <div class="certificate-actions">
                <div class="certificate-btn zuo view" @click="chaLook">
                    <i class="fas fa-eye"></i> 查看
                </div>
                <div class="certificate-btn you download" @click="getPDF(i.CERTIFICATE_ID)">
                    <i class="fas fa-download"></i> 下载
                </div>
            </div>
        </div>
    </div>

    <div class="certificate-no" v-show="this.zhengshushow">
        <span>您还未获取证书</span>
    </div>
</template>
<script>
import loadXMLDoc from '@/store/AjaxRequest.js';
// import loadXMLDoc from '@/store/AjaxRequest.js';
export default {
    data() {
        return {
            // 数据
            cardarr: null,

            //没有证书显示 
            zhengshushow: false,
        }
    },
    created() {
        let value;
        value = localStorage.getItem("userCardArr")
        if (value == null) {
            loadXMLDoc({
                reqBody: {
                    'token': localStorage.getItem('token'),
                    'deviceid': localStorage.getItem("equipment"),
                    "name": "54188",
                },
                reqUrl: "http://8.148.31.153:3030/queryCertificate",
                reqHeader: { 'Content-Type': 'application/json' },
                callback: (error, resdata) => {
                    try {
                        if (!error) {
                            const data = JSON.parse(resdata);
                            this.cardarr = data.resData.certificate
                            localStorage.setItem("userCardArr", resdata)
                        }
                    } catch (error) {
                        console.log(error)
                    }
                }
            })
        } else {
            if (value == "null" || value == "undefined" || value == '') {
                loadXMLDoc({
                    reqBody: {
                        'token': localStorage.getItem('token'),
                        'deviceid': localStorage.getItem("equipment"),
                        "name": "54188",
                    },
                    reqUrl: "http://8.148.31.153:3030/queryCertificate",
                    reqHeader: { 'Content-Type': 'application/json' },
                    callback: (error, resdata) => {
                        try {
                            if (!error) {
                                const data = JSON.parse(resdata);
                                this.cardarr = data.resData.certificate
                                localStorage.setItem("userCardArr", resdata)
                            }
                        } catch (error) {
                            console.log(error)
                        }
                    }
                })
            } else {
                const datas = JSON.parse(localStorage.getItem("userCardArr"))
                this.cardarr = datas.resData.certificate
            }
        }
    },
    methods: {
        chaLook() {
            alert("暂时无法查看，请自行下载")
        },
        // PDF下载
        getPDF(values) {
            const urlEncodedData = {
                token: localStorage.getItem("token"),
                deviceid: localStorage.getItem("equipment"),
                certificateID: values
            };
            try {
                const triggerDirectDownload = () => {
                    const form = document.createElement('form');
                    form.method = 'POST';
                    form.action = 'http://8.148.31.153:3030/downloadCertificate';  // 替换为实际API地址
                    form.style.display = 'none';
                    form.target = '_download-frame';  // 指定到iframe

                    // 添加参数
                    for (const [key, value] of Object.entries(urlEncodedData)) {
                        const input = document.createElement('input');
                        input.type = 'hidden';
                        input.name = key;
                        input.value = value;
                        form.appendChild(input);
                    }

                    // 创建隐藏iframe接收下载
                    const frameName = 'download-frame';
                    let iframe = document.getElementById(frameName);
                    if (!iframe) {
                        iframe = document.createElement('iframe');
                        iframe.name = frameName;
                        iframe.id = frameName;
                        iframe.style.display = 'none';
                        document.body.appendChild(iframe);
                    }

                    // 附加表单并提交
                    document.body.appendChild(form);
                    form.submit();

                    // 清理表单（保留iframe以便下次使用）
                    setTimeout(() => {
                        document.body.removeChild(form);
                    }, 5000);
                };
                triggerDirectDownload();
            } catch (error) {
                console.log(error);
            }
        }

    },
    watch: {
        cardarr(newval) {
            if (newval != null) {
                if (newval.length == 0) {
                    this.zhengshushow = true
                }
            }

        }
    }
};
</script>

